@use '@mview/scss/utilities/variables' as *;

.device-location-map {
  bottom: 0px;
  top: 0px;
  width: calc(100% - 10px);
  position: absolute;
  overflow: hidden;
  margin: 10px;
  margin-left: 0px;
}

::ng-deep div.navigation {
  margin-bottom: 0px !important;
}

.realtime-device-map {
  height: 100%;
  position: relative;
  overflow: hidden;
  background: #f8f9fa;
}

.search-menu {
  background: #f8f9fa;
}

::ng-deep #container {
  height: calc(100% - 240px);
}

::ng-deep #aspnetForm {
  height: 100%;
}

::ng-deep #container #footerBG {
  position: relative;
  width: 100%;
  height: auto;
}

::ng-deep #footerHolder {
  margin: 0;
}

stream-viewer {
  position: absolute;
  left: 310px;
  z-index: 999;
}
